.Navbar {
  list-style-type: none;
  display: flex;
  align-items: center;
  background-color: #0F0F0F;
  position: sticky;
}

.nav-item a {
  display: block;
  color: white;
  text-align: center;
  padding: 1.5rem;
  text-decoration: none;
  color: #008170;
  transition: 0.75s;
}

.active {
  background-color: #008170;
  color: white;
  text-decoration: none;
}

.nav-icon {
  height: 50px;
  margin-right: 1.5rem;
}

.ml-auto {
  margin-left: auto;
}

.nav-item a:hover {
  color: white;
  background-color: #008170;
  cursor: grab;
}

.navbar-toggler {
  background-color: #008170;
  margin-left: 0;
}

.navbar {
  height: 4rem;
}

.navbar-collapse {
  background-color: black;
  background-color: #0F0F0F;
  padding: 0;
}


@media(max-width: 768px) {
  .Navbar {
    padding: 0.8rem;
    margin-top: -.5rem;
  }
}